import axios from 'axios';
export const ForecastTypePaths = {
    ["cost" /* cost */]: 'forecasts/openshift/costs/',
    ["infrastructure" /* infrastructure */]: 'forecasts/openshift/costs/',
    ["supplementary" /* supplementary */]: 'forecasts/openshift/costs/',
};
export function runForecast(forecastType, query) {
    const path = ForecastTypePaths[forecastType];
    return axios.get(`${path}?${query}`);
}
